@import "https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Montserrat:wght@100;200;300;400;500;600;700;800;900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap";
@import "https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&display=swap";
.songcard {
  width: content;
  height: 3em;
  margin-bottom: .2em;
  padding: 1em;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(0,128,128,.322);
  border-radius: 6px;
  color: #fff;
  transition: ease-in 50ms;
  position: relative;
}
.songcard:hover {
  background-color: teal;
  box-shadow: teal 5px 5px 20px;
}
.songcard-left {
  height: 1em;
  flex-wrap: nowrap;
  display: flex;
  align-items: center;
}
.songcard-left .songcard-title {
  text-overflow: ellipsis;
}
.songcard-artist {
  font-size: .75em;
}
.songcard-right {
  display: flex;
  justify-content: flex;
  gap: .5em;
}
.play-button {
  transition: 150ms all ease-in-out;
  opacity: 0;
  cursor: pointer;
  padding: .25em;
}
.play-button.showing {
  opacity: 1;
}
.play-button.showing :hover>* {
  color: var(--clr-cta-primary);
}